const Layout = () => import('@/views/layout')
const res = []
// const data = [
//   { path: 'reportforms', meta: { title: '模块1', roles: ['djlun', 'admin']}, children: [
//     { path: 'executive', meta: { title: '标题1', roles: ['admin'] }, children: [
//       { path: 'executive1', meta: { title: '指标1', roles: ['admin'] } },
