//       { path: 'research1', meta: { title: '指标1', roles: ['admin'] } },
//       { path: 'research2', meta: { title: '指标2', roles: ['admin'] } },
//     ]},
//     { path: 'produce', meta: { title: '标题3', roles: ['admin'] }, children: [
//       { path: 'produce1', meta: { title: '指标1', roles: ['admin'] } },
//       { path: 'produce2', meta: { title: '指标2', roles: ['admin'] } },
//       { path: 'produce3', meta: { title: '指标3', roles: ['admin'] } },
//       { path: 'produce4', meta: { title: '指标4', roles: ['admin'] } },
//       { path: 'produce5', meta: { title: '指标5', roles: ['admin'] } },
//     ]},
//     { path: 'sales', meta: { title: '标题4', roles: ['admin'] }, children: [
//       { path: 'sales1', meta: { title: '指标1', roles: ['admin'] } },
//       { path: 'sales2', meta: { title: '指标2', roles: ['admin'] } },
//       { path: 'sales3', meta: { title: '指标3', roles: ['admin'] } },
//       { path: 'sales4', meta: { title: '指标4', roles: ['admin'] } },
//       { path: 'sales5', meta: { title: '指标5', roles: ['admin'] } },
//     ]},
//   ]},
//   { path: 'dataentry', meta: { title: '模块2', roles: ['djlun', 'admin']}, children: [
//     { path: 'datacheck', meta: { title: '标题1', roles: ['admin'] } },
//     { path: 'dataimport', meta: { title: '标题2', roles: ['admin'] } },
//   ]},
//   { path: 'maindata', meta: { title: '模块3', roles: ['djlun', 'admin']}, children: [
//     { path: 'maindatasearch', meta: { title: '标题1', roles: ['admin'] } },
//     { path: 'maindatamanage', meta: { title: '标题2', roles: ['admin'] } },
//   ]},
//   { path: 'system', meta: { title: '模块4', roles: ['djlun', 'admin']}, children: [
//     { path: 'systemmanage', meta: { title: '标题1', roles: ['admin'] } },
//   ]}
// ]
const data = [
  { path: 'reportforms', meta: { title: '报表模块', roles: ['pczg']}, children: [
    { path: 'executive', meta: { title: '高管看板', roles: ['pczg'] }, children: [
      { path: 'executive1', meta: { title: '关键指标看板', roles: ['pczg'] } },
      { path: 'executive2', meta: { title: '阿米巴报表', roles: ['pczg'] } },
    ]},
    { path: 'research', meta: { title: '研发主题', roles: ['pczg'] }, children: [
      { path: 'research1', meta: { title: '研发项目管控', roles: ['pczg'] } },
      { path: 'research2', meta: { title: '研发投入分析', roles: ['pczg'] } },
    ]},
    { path: 'produce', meta: { title: '生产主题', roles: ['pczg'] }, children: [
      { path: 'produce1', meta: { title: '不良率分析', roles: ['pczg'] } },
      { path: 'produce2', meta: { title: '采购周期分析', roles: ['pczg'] } },
      { path: 'produce3', meta: { title: '生产周期分析', roles: ['pczg'] } },
      { path: 'produce4', meta: { title: '单台成本分析', roles: ['pczg'] } },
      { path: 'produce5', meta: { title: '库存周转天数分析', roles: ['pczg'] } },
    ]},
    { path: 'sales', meta: { title: '销售主题', roles: ['pczg'] }, children: [
      { path: 'sales1', meta: { title: '月度绩效总表', roles: ['pczg'] } },
      { path: 'sales2', meta: { title: '按预测数据完成情况表', roles: ['pczg'] } },
      { path: 'sales3', meta: { title: '区域经理绩效数据统计表', roles: ['pczg'] } },
      { path: 'sales4', meta: { title: 'PIP明细表', roles: ['pczg'] } },
      { path: 'sales5', meta: { title: 'PIP排名表', roles: ['pczg'] } },
    ]},
  ]},
  { path: 'dataentry', meta: { title: '数据录入', roles: ['djlun', 'pczg']}, children: [
    { path: 'datacheck', meta: { title: '数据状态检查', roles: ['pczg'] } },
    { path: 'dataimport', meta: { title: '数据导入', roles: ['pczg'] } },
  ]},
  { path: 'maindata', meta: { title: '主数据管理', roles: ['djlun', 'pczg']}, children: [
    { path: 'maindatasearch', meta: { title: '主数据查询', roles: ['pczg'] } },
    { path: 'maindatamanage', meta: { title: '主数据管理', roles: ['pczg'] } },
  ]},
  { path: 'system', meta: { title: '系统管理', roles: ['djlun', 'pczg']}, children: [
    { path: 'systemmanage', meta: { title: '系统管理', roles: ['pczg'] } },
  ]}
]

data.forEach(el => {
  const children = []
  el.children.forEach(item => {
    if (item.children) {
      const child = []
      item.children.forEach(list => {
        child.push({
          path: list.path,
          component: () => import(`@/views/${el.path}/${item.path}/${list.path}`),
          name: list.path,
          meta: list.meta,
        })
      })
      children.push({
        path: item.path,
        component: () => import(`@/views/${el.path}/${item.path}/index`),
        name: item.path,
        meta: item.meta,
        children: child
      })
    } else {
      children.push({
        path: item.path,
        component: () => import(`@/views/${el.path}/${item.path}`),
        name: item.path,
        meta: item.meta
      })
    }
  })
  res.push({
    path: `/${el.path}`,
    component: Layout,
    meta: el.meta,
    children: children
  })
})

export default res



